/* ------------------------------Scroll Webkit ------------------------------- */
::-webkit-scrollbar {
    width: 12px;
    height: 100px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgb(226, 143, 143);
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background:rgb(164, 153, 153);
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: grey;
  }
  /* ------------------------------Scroll Webkit ------------------------------- */

.nav{
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    width:100%;
    position: fixed;
    top:0;
    z-index: 3;
    background-color: white;
    display:flex;
    align-items: center;
    justify-content: space-between;
}
.ranjan-logo{
    width: 15%;
    border-radius: 50px;
    /* border: 3px solid red; */

}
.nav1{
    margin-left: 20px;
}
.nav2{
    font-size: 20px;
    display: flex;
    gap:15px;
    margin-right: 40px;
    margin: 15px 10px 15px 10px;
}

.nav2 button{
    background-color: rgb(235, 232, 232);
    width: 90px;
    border-radius: 5px;
    padding: 3px;
    font-weight: 500;
    font-size: 17px;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    border: 2px solid white;
}

.nav2 button:hover{
    /* background-color: rgb(48, 47, 47); */
    border: 2px solid black;
    color:black;
}
.nav3{
    display: none;
}
.colormode{
    cursor: pointer;
}
@media screen and (max-width: 768px){
    .nav{
        width: 100%;
    }
    .ranjan-logo{
        width: 15%;
        display: flex;
        justify-content: space-between;
    
    }
    .nav2{
        font-size: 15px;
        gap: 10px;
        margin: 15px 0;
        display: none;
    }
    .nav3{
        display: inline-block;
        margin-right: 29px;

    }
}
@media screen and (max-width: 380px){
    .nav2{
        display: none;
    }
    .nav{
        width: 100%;
    }
    .ranjan-logo{
        width: 18%;
    }
    .nav3{
        display: inline-block;
        margin-right: 29px;

    }
}
@media screen and (max-width: 480px){
    .nav2{
        display: none;
    }
    .nav3{
        display: inline-block;
        margin-right: 40px;
    }
}